.TodoTmplate {
    width: 512px;
    margin: 0 auto;
    margin-top: 6rem;
    border-radius: 4px;
    overflow: hidden;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
    background: white;

    .app-title {
        color: white;
        background-color: #22b8cf;
        height: 4rem;
        font-size: 1.5rem;
        display: flex;
        justify-content: space-between; // 왼쪽: 텍스트 / 오른쪽: 아이콘
        align-items: center;
        font-weight: 600;
        font-family: 'Pretendard', sans-serif;
        padding: 0 1.25rem;

    .dark-toggle {
        background: none;
        border: none;
        cursor: pointer;
        font-size: 1.5rem;
        color: white;
        transition: color 0.3s;

        &:hover {
            color: #e3fafc;
        }
    }
}

    .content {
        background-color: white;
    }
}