.Todolistitem {
    display: flex;
    align-items: center;
    padding: 1rem;
    margin-bottom: 0.75rem;
    border-radius: 8px;
    border: 1px solid #e3e7eb; 
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.04);
    background: white;
    animation: fadeIn 0.3s ease-in;

    &:nth-child(even) {
        background: #f8f9fa;
    }

    .checkbox {
        flex: 1;
        display: flex;
        align-items: center;
        cursor: pointer;
        font-family: 'Pretendard', sans-serif;

        svg { // 아이콘
            font-size: 1.5rem;
            color: #ced4da;
        }

        .text {
            margin-left: 0.75rem;
            font-size: 1.125rem;
            flex: 1; // 차지할 수 있는 영역 모두 차지 
            color: #495057;
        }

        // 체크되었을 때 보여줄 스타일 
        &.checked {
            
            svg { // 아이콘
                color: #22b8cf;
            }
    
            .text {
                text-decoration: line-through;
                color: #adb5bd;
            }
        }
    }

    .remove {
        display: flex;
        align-items: center;
        font-size: 1.25rem;
        color: #ff6b6b;
        cursor: pointer;
        transition: color 0.2s;

        svg {
            font-size: 1.4rem;
        }

        &:hover {
            color: #ff8787;
        }
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(8px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}