.TodoInsert {
    display: flex;
    border-top: 1px solid #e9ecef;
    background: #f1f3f5;

    input {
        background: none;
        outline: none;
        border: none;
        padding: 1rem;
        padding-left: 1.25rem; 
        font-size: 1.125rem;
        line-height: 1.5;
        color: #495057;
        font-family: 'Pretendard', sans-serif;
        &::placeholder {
            color: #868e96;
        }
        flex: 1; // 버튼을 제외한 영역을 모두 차지하기
    }

    button {
        // 기본 스타일 초기화 
        background: none;
        outline: none;
        border: none;
        background: #22b8cf;
        color: white;
        padding: 0 1.25rem;
        font-size: 1.5rem;
        display: flex; // <MdAdd /> 아이콘을 수직 가운데 정렬하기 위해 삽입
        align-items: center;
        cursor: pointer;
        transition: background 0.2s ease-in; // background 빨간색 보기 싫으면 all로 바꿔도됨
        &:hover {
            background: #3bc9db;
        }

        &.shake {
            background: #ff6b6b;
            animation: shake 0.3s ease-in-out;
        }
    }
}

@keyframes shake {
    0% { transform: translateX(0); }
    25% { transform: translateX(-4px); }
    50% { transform: translateX(4px); }
    75% { transform: translateX(-4px); }
    100% { transform: translateX(0); }
}
